.a {
	width: 100%;
	height: 100vh;
	background-size: cover;
}
.b {
	width: 100%;
	height: 100vh;
	background-size: cover;
}
.c {
	width: 100%;
	height: 100vh;
	background-size: cover;
}
.typer {
	margin: "2% 0";
	font-size: 1.5rem;
	font-family: inherit;
	line-height: 1.2;
	color: inherit;
	font-weight: 500;
	touch-action: none;
}
.mouse-icon {
	position: relative;
	border: 2px solid #fff;
	border-radius: 16px;
	height: 50px;
	width: 30px;
	margin: 0 auto;
	display: block;
	z-index: 10;
	.wheel {
		-webkit-animation-name: drop;
		-webkit-animation-duration: 1s;
		-webkit-animation-timing-function: linear;
		-webkit-animation-delay: 0s;
		-webkit-animation-iteration-count: infinite;
		-webkit-animation-play-state: running;
		animation-name: drop;
		animation-duration: 1s;
		animation-timing-function: linear;
		animation-delay: 0s;
		animation-iteration-count: infinite;
		animation-play-state: running;
		position: relative;
		border-radius: 10px;
		background: #fff;
		width: 4px;
		height: 10px;
		top: 4px;
		margin-left: auto;
		margin-right: auto;
	}
}
.link {
	font-size: 1.25rem;
	margin-bottom: 0.5rem;
	font-family: inherit;
	font-weight: 500;
	line-height: 1.2;
	color: inherit;
}
.not-active {
	cursor: default;
	text-decoration: none;
	color: inherit;
	&:hover {
		cursor: default;
		text-decoration: none;
		color: black;
	}
}
.control-dots {
	display: inline-block;
	width: 50px;
}
.bannerSize {
	position: relative;
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	background-attachment: fixed;
	min-height: 100%;
	height: 85vh;
	filter: saturate(110%) brightness(1.5) contrast(125%) blur(1px);
	filter: hue-rotate(10deg);
	touch-action: none;
}
.opacity {
	opacity: 1.1;
	filter: drop-shadow(0px 4px 10px black);
}


@supports (-webkit-backdrop-filter: none) or (backdrop-filter: none) {
	.intro-content {
		backdrop-filter: blur(2px);
	}
}

@-webkit-keyframes drop {
	0% {
		top: 5px;
		opacity: 0;
	}

	30% {
		top: 10px;
		opacity: 1;
	}

	100% {
		top: 25px;
		opacity: 0;
	}
}

@keyframes drop {
	0% {
		top: 5px;
		opacity: 0;
	}

	30% {
		top: 10px;
		opacity: 1;
	}

	100% {
		top: 25px;
		opacity: 0;
	}
}